.background-parent {

    background-color: #342547;

    overflow-x: hidden;
    padding-top: 80px;

}

.custom-background {

    width: 99%;
    height: 100%;
    margin: 0 auto;
    border-top-left-radius: 40px;
    border-top-right-radius: 40px;
    background: linear-gradient(180deg,
            rgba(118, 91, 147, 0.70) 0%,
            rgba(31, 15, 48, 0.00) 100%);
    display: flex;
    position: relative;
    left: 3.5%;

}

.banner-image-box {
    width: 100%;
    flex: 1;
    padding: 0 20px;
}

.discover-img {
    width: 100%;
    margin-top: -50px;
    height: auto;
}

.discover-text-div {
    margin-bottom: 10%;

}

.text-box-position {
    position: inherit;
}

.discover-heading {
    font-family: 'Poppins';
    font-size: 26px;
    font-style: normal;
    font-weight: 700;
    line-height: 170%;
    color: #FFF;

    letter-spacing: 12px;


}

.discover-text {
    color: #FFF;
    font-family: 'Poppins';
    font-size: 18px;
    font-style: normal;
    font-weight: 700;

    flex-wrap: wrap;
    line-height: 200%;
    letter-spacing: 1.68px;

    width: 75%;
}

.discover-sub_text {
    color: #FFF;
    font-family: 'Poppins';
    font-size: 16px;

    font-weight: 700;
    margin-bottom: 4px;

}

.discover-download {
    color: #FFF;
    font-family: 'Poppins';
    font-size: 16px;

    font-weight: 700;

}

.iosimage-div {
    display: flex;

}

.googleimage {

    height: 40px;
    width: 120px;
    margin-right: 20px;

}

.iosimage {

    flex: 0 0 150px;
    height: 40px;
    width: 120px;


    margin-left: 0%;
}

.banner-image-box.col-md-6 {
    margin-right: -115px;
}

@media (max-width:375px) {
    .iosimage {
        position: relative;


    }

    .iosimage-div {
        flex-wrap: wrap;

    }

    .custom-background {
        flex-direction: column-reverse;
    }
}

@media (max-width:425px) {
    .discover-img {
        margin-top: 0px !important;
    }
}

@media (min-width:1900px) {
    .discover-download {
        bottom: 0%;
    }

    .iosimage-div {
        bottom: 0%;
    }
}

@media(max-width: 700px) {
    .banner-image-box.col-md-6 {
        margin-right: 0 !important;
    }
}