.images-slider-box .swiper {
    width: 100%;
    height: 100%;
    background-color: #342547;
    overflow: hidden;

}

.images-slider-box .swiper .swiper-slide {
    text-align: center;
    font-size: 18px;
    background-color: #342547;
    height: 100%;
    width: 33%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.image-container {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid white !important;
    border-radius: 5px;

}

.slider-image {
    width: 100%;
    height: 100%;
}

.image-container :first-child {
    border-right: none !important;
}

.image-container :last-child {
    border-right: none !important;

}

.image-container img {
    max-width: 100%;
    max-height: auto;
    object-fit: cover;

    border-radius: 5px;
}


.centered-text1 {
    position: absolute;

    top: 50%;
    font-size: 25px;

    left: 50%;

    transform: translate(-50%, -50%);

    color: #FFF;
    flex-wrap: wrap;

    font-family: 'Poppins';

    font-weight: 700;
    line-height: normal;
}

.swiper-slide img {
    max-width: 100%;
    max-height: auto;
    object-fit: cover;
}


.images-slider-box .swiper::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0%;
    right: 55%;
    bottom: 0;
    pointer-events: none;
    background: linear-gradient(to right, #342547 50%, transparent 50%);
    z-index: 10;
    clip-path: polygon(30% 0%, 0 0, 0 100%, 30% 100%, 0% 75%, 0% 25%);
}


.images-slider-box .swiper::before {
    content: '';

    position: absolute;
    top: 0;
    left: 55%;
    right: 0%;
    bottom: 0;
    pointer-events: none;
    background: linear-gradient(to left, #342547 50%, transparent 50%);
    z-index: 10;

    clip-path: polygon(80% 0%, 100% 25%, 100% 75%, 80% 100%, 100% 100%, 100% 0);
}

.swiper-slide-next,
.swiper-slide-prev {
    transform: scale(0.8);

}